.endscreen,
.quickstart {
    background-color: rgba(0, 0, 0, 0.95);
    color: #fff;
    padding: 2vw;
    border-radius: 0.5vw;
    z-index: 5000;
    width: 20vw;
}

.endscreen > div {
    font-size: 2vw;
    text-align: center;
}

.endscreen > .title {
    font-size: 15vw;
}

.quickstart,
.endscreen {
    width: 32vw;
    position: absolute;
    top: 6vw;
    right: 32vw;
}

.quickstart button,
.endscreen button {
    font-size: 4vw;
    color: white;
    border-radius: 0.5vw;
    border-color: gray;
    border-style: solid;
    height: 10vw;
    background-color: rgba(0, 0, 0, 0);
    width: 14vw;
    margin: 1vw;
}

.quickstart button > div,
.endscreen button > div {
    font-size: 1vw;
    color: lightgray;
}

.quickstart .left {
    float: left;
    width: 16vw;
}

.quickstart .right {
    float: right;
    width: 16vw;
}

.quickstart .title {
    font-size: 2vw;
    margin-bottom: 2vw;
}


.header-clock,
.header-title {
    margin-top: 0.25vw;
    float: left;
    font-size: 2vw;
}

.header-title {
    width: 35vw;
}

.header-clock {
    padding-left: 5.3vw;
}

.header-buttons {
    float: right;
    padding-right: 1vw;
}

.header-buttons > div {
    float: left;
    padding-left: 0.5vw;
}

.icon-container {
    width: 5vw;
    float: left;
    text-align: center;
}

.heart-container {
    width: 13vw;
    float: left;
    padding-left: 0.5vw;
    text-align: left;
}

.header-buttons > div > button {
    font-size: 2vw;
    padding-top: 0vw;
    border-radius: 0.5vw;
    border-color: gray;
    border-style: none;
    width: 5vw;
    margin-top: 0.25vw;
    background-color: transparent;
}
.menu {
    position: absolute;
    top: 6vw;
    border-radius: 0.5vw;
    z-index: 6000;
    background-color: rgba(0, 0, 0, 0.95);
    color: white;
    right: 2vw;
    padding: 2vw;
    width: 25vw;
}

.menu .title {
    font-size: 2vw;
    margin-bottom: 2vw;
}

.menu > .section {
    font-size: 1vw;
    margin-bottom: 6vw;
}

.menu > .section:last-child {
    margin-bottom: 0vw;
}

.menu > .section > .title {
    font-size: 1.5vw;
    width: 25vw;
    float: left;
}

.menu > .section > .row > select {
    float: right;
    margin-left: 2vw;
    width: 8vw;
    font-size: 1vw;
}

.menu > .section > .row > .label {
    float: left;
    width: 15vw;
    font-size: 1w;
    color: lightgray;
}

.menu > .section > .row {
    clear: both;
}

